import React, { memo } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { MenuItem, Select, TextField } from '@material-ui/core';
import { useIntl } from 'gatsby-plugin-react-intl';
import NumberFormat from 'react-number-format';
import createNumberMask from 'text-mask-addons/dist/createNumberMask';
import MaskedInput from 'react-text-mask';
import classNames from 'classnames';

// Utils
import { getCurrencyFormate } from '../utils/utils';

const useStyles = makeStyles((theme) => ({
    input: {
        marginTop: '10px',
    },
    formWrapper: {
        minHeight: '400px',
        margin: '0 auto 35px',
        width: '80%',
        [theme.breakpoints.down('xs')]: {
            width: '100%',
        },
    },
    section: {
        marginLeft: '0px',
    },
    arraySection: (useInlineLayout) => {
        if (useInlineLayout) {
            return {
                display: 'inline-block',
                marginTop: '10px',
            };
        }

        return {
            display: 'block',
        };
    },
    groupedFieldWrapper: (useInlineLayout) => {
        const mobile = {
            [theme.breakpoints.down('xs')]: {
                width: '100%',
            },
        };

        if (useInlineLayout) {
            return {
                display: 'flex',
                marginRight: '10px',
                ...mobile,
            };
        }

        return {
            display: 'inline',
            ...mobile,
        };
    },
    select: (useInlineLayout) => ({
        marginTop: '16px',
    }),
    field: (useInlineLayout) => {
        if (useInlineLayout) {
            return {
                marginRight: '2%',
            };
        }

        return {
            width: '48%',
            marginRight: '2%',
        };
    },
    textArea: {
        width: '98%',
    },
    buttonWrapper: {
        marginTop: '15px',
    },
    removeButton: {
        marginLeft: '10px',
    },
    yearTitle: {
        display: 'inline',
    },
}));

const NumberFormatCustom = (props) => {
    const { inputRef, onChange, ...other } = props;
    const intl = useIntl();
    const { locale } = intl;
    const extraProps = {
        ...getCurrencyFormate(locale),
    };

    return (
        <NumberFormat
            // eslint-disable-next-line react/jsx-props-no-spreading
            {...other}
            getInputRef={inputRef}
            onValueChange={(values) => {
                onChange({
                    target: {
                        name: props.name,
                        value: values.value,
                    },
                });
            }}
            isNumericString
            // eslint-disable-next-line react/jsx-props-no-spreading
            {...extraProps}
        />
    );
};

const PercentMaskedInput = (props) => {
    const { inputRef, value, ...other } = props;
    const percentMask = createNumberMask({
        allowDecimal: true,
        prefix: '',
        suffix: '%',
        integerLimit: 3,
    });

    return (
        <MaskedInput
            // eslint-disable-next-line react/jsx-props-no-spreading
            {...other}
            ref={(ref) => {
                inputRef(ref ? ref.inputElement : null);
            }}
            mask={percentMask}
            showMask={Boolean(value)}
            value={value}
            guide={false}
            keepCharPositions
        />
    );
};

const FormikInput = ({
    name,
    rows,
    rowsMax = 10,
    fullWidth = true,
    onChange,
    error,
    value,
    type = 'text',
    options = [],
}) => {
    const intl = useIntl();
    const classes = useStyles();
    let [year, inputName] = name.split('.');
    if (!inputName) {
        inputName = name;
    }

    if (options.length > 0) {
        const placeholderValue = intl.formatMessage({ id: 'builder.none' });
        return (
            <Select
                className={classNames(classes.field, classes.select)}
                label={intl.formatMessage({ id: `builder.${inputName}` })}
                placeholder={intl.formatMessage({ id: `builder.${inputName}` })}
                fullWidth
                id={name}
                name={name}
                value={value || placeholderValue}
                onChange={onChange}
            >
                <MenuItem
                    value={placeholderValue}
                    disabled
                >
                    {intl.formatMessage({ id: 'builder.selectType' })}
                </MenuItem>
                {options.map((val) => (
                    <MenuItem
                        key={val}
                        value={val}
                    >
                        {intl.formatMessage({ id: `builder.${val}` })}
                    </MenuItem>
                ))}
            </Select>
        );
    }

    let InputProps = {};
    if (type === 'currency') {
        InputProps = {
            ...InputProps,
            inputComponent: NumberFormatCustom,
        };
    } else if (type === 'percentage') {
        InputProps = {
            ...InputProps,
            inputComponent: PercentMaskedInput,
        };
    }

    return (
        <TextField
            className={classes.input}
            multiline={rows > 1}
            rows={rows}
            rowsMax={rowsMax}
            fullWidth={fullWidth}
            id={name}
            name={name}
            label={intl.formatMessage({ id: `builder.${inputName}` })}
            value={value}
            onChange={onChange}
            error={Boolean(error)}
            helperText={error}
            InputLabelProps={{ shrink: Boolean(value) }}
            // inputProps={inputProps}
            // eslint-disable-next-line react/jsx-no-duplicate-props
            InputProps={InputProps}
        />
    );
};

export default memo(FormikInput);
